import { FETCH_MESSAGES, MESSAGE_POSTED, SELECT_CHANNEL } from '../actions';

export default function(state = null, action) {
  switch (action.type) {
    case FETCH_MESSAGES : {
      return action.payload.messages;
    }
    case MESSAGE_POSTED : {
      const newState = state.slice(0);
      newState.push(action.payload);
      return newState;
    }
    case SELECT_CHANNEL : {
      return [];
    }
    default:
      return state;
  }
}
